
// Табы и Блоки
$(document).ready(function() {
    $(".lists__trip a").click(function (e) {

        e.preventDefault(); //Запрещает добавление в строку ссылку элемента
        
        $('.lists__trip a').removeClass('active__lists__trip');
        $('.Blocks').removeClass('trip__active').fadeOut(1);


        $(this).addClass('active__lists__trip').fadeIn(800);
        $($(this).attr('href')).addClass('trip__active').fadeIn(800);

    });
    $(".lists__trip a:first").click();
    
});

